<!--
  @auth:lcw
  @date:2020-4-9
  详情列表（件次、人次）
-->
<template>
  <div
    class="detail-list"
    v-loading="!listData.length"
    :element-loading-background="$STANDARD.loadingBgColor"
  >
    <div class="inner">
      <ul>
        <li
          class="item"
          v-for="(item, index) of listData.slice(0, breakNumber)"
          :key="index"
        >
          <p class="label" :title="item.name">{{ item.name }}</p>
          <div class="item-detail">
            <p class="jc">
              <incNumber
                class="number"
                :incNumber="item[mapKey[getArrayIndex('信访件次')]]"
                :style="{ width: numMaxLength * 15 + 'px' }"
              ></incNumber>
              <span class="number-unit">件次</span>
              <incNumber
                class="percent"
                :incNumber="
                  (item[mapKey[getArrayIndex('件次占比')]] * 100).toFixed(2)
                "
              ></incNumber>
              <span>{{ item[mapKey[getArrayIndex("件次占比")]] }}</span>
              <span class="per-unit">%</span>
            </p>
            <div></div>
            <!-- <p class="rc">
              <incNumber class="number" :incNumber="item[mapKey[getArrayIndex('信访人次')]]"
                         :style="{width:numMaxLength*15+'px'}"></incNumber>
              <span class="number-unit">人次</span>
              <incNumber class="percent" :incNumber="(item[mapKey[getArrayIndex('人次占比')]]*100).toFixed(2)"></incNumber>
              <span class="per-unit">%</span>
            </p> -->
          </div>
        </li>
      </ul>
      <ul v-if="breakNumber !== 100 && listData.length > breakNumber">
        <li
          class="item"
          v-for="(item, index) of listData.slice(breakNumber)"
          :key="index"
        >
          <p class="label" :title="item.name">{{ item.name }}</p>
          <div class="item-detail">
            <p class="jc">
              <incNumber
                class="number"
                :incNumber="item[mapKey[getArrayIndex('信访件次')]]"
                :style="{ width: numMaxLength * 15 + 'px' }"
              ></incNumber>
              <span class="number-unit">件次</span>
              <incNumber
                class="percent"
                :incNumber="
                  (item[mapKey[getArrayIndex('件次占比')]] * 100).toFixed(2)
                "
              ></incNumber>
              <span class="per-unit">%</span>
            </p>
            <div></div>
            <p class="rc">
              <incNumber
                class="number"
                :incNumber="item[mapKey[getArrayIndex('信访人次')]]"
                :style="{ width: numMaxLength * 15 + 'px' }"
              ></incNumber>
              <span class="number-unit">人次</span>
              <incNumber
                class="percent"
                :incNumber="
                  (item[mapKey[getArrayIndex('人次占比')]] * 100).toFixed(2)
                "
              ></incNumber>
              <span class="per-unit">%</span>
            </p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import incNumber from "@/components/incNumber/index";

export default {
  name: "detailList",
  components: {
    incNumber
  },
  props: {
    params: {
      type: [Object, Array],
      default: null
    },
    // 一列显示多少条，默认只显示一列（给了个较大值100）
    breakNumber: {
      type: Number,
      default: 100
    }
  },
  data() {
    return {
      mapKey: [],
      mapValue: [],
      numMaxLength: 0,
      listData: []
    };
  },
  mounted() {
    this.setLength();
    // if(this.params){
    //   this.mapKey = Object.keys(this.params.map);
    //   this.mapValue = Object.values(this.params.map);
    //   console.log(this.mapKey);
    //   console.log(this.mapValue);
    // }
  },
  methods: {
    setLength() {
      this.mapKey = Object.keys(this.params.map);
      this.mapValue = Object.values(this.params.map);
      let numMaxArr = [];
      if (this.params instanceof Array) {
        this.listData = this.params;
      } else {
        this.listData = this.params.data;
      }
      this.listData.filter(item => {
        numMaxArr.push(
          Math.max(item.value_g0_1, item.value_g2_1).toString().length
        );
      });
      this.numMaxLength = Math.max.apply(null, numMaxArr);
    },
    // 获取某个值在数组中的下标
    getArrayIndex(obj) {
      let i = this.mapValue.length;
      while (i--) {
        if (this.mapValue[i] === obj) {
          return i;
        }
      }
      return -1;
    }
  },
  computed: {
    styleType() {
      return this.$store.state.styleType;
    }
  },
  watch: {
    params() {
      this.setLength();
    }
  }
};
</script>

<style lang="stylus" scoped>
.detail-list
  position relative
  width 100%
  height 100%
  display flex
  justify-content center
  align-items center
  .inner
    width 100%
    font-size 0
    display flex
    ul
      flex 1
      li.item
        display flex
        font-size 0
        margin 16px 0
        .label
          flex 1
          /*width 1rem*/
          text-align right
          font-size 16px
          color #fff
          margin-right 20px
          line-height 24px
          white-space nowrap
          overflow hidden
          text-overflow ellipsis

        .item-detail
          flex 2
          p
            font-size 0
            line-height 20px
            display inline-block
            .number
              display inline-block
              font-size 24px
              margin-right 20px
            .number-unit
              font-size 16px
              margin-right 16px
            .percent
              font-size 16px
              color #ffde00
            .per-unit
              font-size 16px
              color #ffde00
          .jc
            .number
              color #13fbff
            .number-unit
              color #13fbff
          .rc
            .number
              color #128f9e
            .number-unit
              color #128f9e
</style>
