<template>
  <div class="wrapper">
    <div class="content">
      <div class="c-left">
        <div class="c-top">
          <div class="r-left">
            <ctitle :title="'信访总量'"></ctitle>
            <div class="inner">
              <div class="detail">
                <allCount class="all-count" :count="leftallcount"></allCount>
              </div>
              <div class="chart">
                <pie-chart3D :params="zbPieParams"></pie-chart3D>
              </div>
            </div>
          </div>
          <div class="r-right">
            <ctitle :title="'整体化解率'"></ctitle>
            <div class="inner">
              <cir-circle :count="hjvValue"></cir-circle>
            </div>
          </div>
        </div>
        <div class="c-btm">
          <div class="part">
            <ctitle :title="'年度信访件总量、化解趋势'"></ctitle>
            <LinesR class="chart" :params="zsLineParams"></LinesR>
          </div>
        </div>
      </div>
      <div class="c-right">
        <ctitle :title="'国家交办、督办矛盾化解信访件'"></ctitle>
        <div class="r-top">
          <div class='r-top-l'>
            <allCount class="all-count" :count="allcount"></allCount>
            <div class="detail">
              <detailList :params="detailList"></detailList>
            </div>
          </div>
          <div class="inner2">
            <div class="chart">
              <pie-chart3D :params="jbPieParams"></pie-chart3D>
              <p class="jb">交办</p>
            </div>
            <div class="chart">
              <pie-chart3D :params="dbPieParams"></pie-chart3D>
              <p class="jb">督办</p>
            </div>
          </div>
        </div>
        <div class="r-bottom">
          <ctitle :title="'省、市、县登记矛盾化解信访件'"></ctitle>
          <div class="inner3">
            <div class="chart" v-for="(item, index) of djjgArr" :key="index">
              <loopPie :params="item.params"></loopPie>
              <p class="label">{{ item.name }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { get_http } from "@/api/index";
import { getCurMonth } from "@/utils/time";
import ctitle from "@/components/title/index";
import incNumber from "@/components/incNumber/index";
import PieChart3D from "@/components/pie/PieChart3D";
import LinesR from "@/components/line/LinesR";
import allCount from "@/components/list/allCount";
import detailList from "@/components/mdfxyjyp/xzfx";
import cirCircle from "@/components/cirCircle/index";
import loopPie from "@/components/xzfx/LoopPie";
import timeChange from "@/components/timeChange/index";

export default {
  name: "xzfx",
  components: {
    ctitle,
    incNumber,
    PieChart3D,
    LinesR,
    allCount,
    detailList,
    cirCircle,
    loopPie,
    timeChange
  },
  data() {
    return {
      // 信访形式走势
      xfxszsList: [],
      // 1.信访总量-总数
      mdgjhjjc:'',
      leftallcount:-1,
      // 2.信访总量-饼图
      xfxs:'',
      mdgjhjjc:'',
      zbPieParams: {
        data: [],
        color: ["#655CDD", "#50B3FF", "#62EDAC"],
        angle: 20,
        radius: "40%",
        labelRadius: 30,
        styleObj: {
          width: "100%",
          height: "100%"
        },
        isBr: true,
        labelPer: true
      },
      // 3.化解率
      hjvValue: -1,
      // 4.年度信访件总量、化解趋势
      zsLineParams: {
        data: [],
        colors: [
          {
            line: "rgba(19, 240, 255, 1)",
            start: "rgba(19, 240, 255, .5)",
            end: "rgba(19, 240, 255, .5)"
          },
          {
            line: "rgba(78, 237, 153, 1)",
            start: "rgba(78, 237, 153, .5)",
            end: "rgba(78, 237, 153, .5)"
          },
          {
            line: "rgba(217, 62, 117, 1)",
            start: "rgba(217, 62, 117, .5)",
            end: "rgba(217, 62, 117, .5)"
          },
          {
            line: "rgba(204, 190, 81, 1)",
            start: "rgba(204, 190, 81, .5)",
            end: "rgba(204, 190, 81, .5)"
          },
          {
            line: "rgba(235, 144, 2, 1)",
            start: "rgba(235, 144, 2, .5)",
            end: "rgba(235, 144, 2, .5)"
          },
          {
            line: "rgba(27, 161, 255, 1)",
            start: "rgba(27, 161, 255, .5)",
            end: "rgba(27, 161, 255, .5)"
          }
        ],
        option: {
          axisLabelColor: "#fff",
          yaxisLabelColor: "#82d8ff",
          legendfontSize: 18,
          splitLineShow: true,
          legenditemWidth: 22,
          legenditemHeight: 4,
          axisLabelShow: true,
          axisTickShow: false,
          axisCategoryName: "",
          // axisYName: "件次",
          axisLabelSize: 14,
          scales: true,
          axisLabelFontSize: 16,
          yaxisLabelFontSize: 14,
          Tofixed: false,
          smooth: true,
          areaShow: 0,
          smoothMonotone: "none",
          dataZoomshow: false,
          legendIcon: "rect",
          left: "5%",
          right: "10%",
          top: "15%",
          bottom: "10%",
          legendTop: "0%",
          legendRight: "10%"
        },

        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        map: {},
        xName: "name"
      },
      // 5.国家交办、督办矛盾化解信访件-总数
      allcount: -1,
      // 6.国家交办、督办矛盾化解信访件-列表
      jsfs:'',
      main_name:'信访件次,件次占比',
      detailList: {
        data:[],
        map:{}
      },
      // 7.国家交办、督办矛盾化解信访件-交办
      jbfs:'交办',
      jbPieParams: {
        data: [],
        color: ["#655CDD", "#50B3FF", "#62EDAC"],
        angle: 20,
        radius: "45%",
        labelRadius: 30,
        styleObj: {
          width: "100%",
          height: "100%"
        },
        isBr: true,
        labelPer: true
      },
      // 8.国家交办、督办矛盾化解信访件-督办
      dbfs:'督办',
      dbPieParams: {
        data: [],
        color: ["#655CDD", "#50B3FF", "#62EDAC"],
        angle: 20,
        radius: "45%",
        labelRadius: 30,
        styleObj: {
          width: "100%",
          height: "100%"
        },
        isBr: true,
        labelPer: true
      },
      //9.省、市、县登记矛盾化解信访件
      djjgArr: [
        // { name: "国家", params: { data: [], options: {} } },
        { name: "省级", params: { data: [], options: {} } },
        { name: "市级", params: { data: [], options: {} } },
        { name: "县级", params: { data: [], options: {} } }
      ],
      djjgPieParams: {
        options: {
          colors: this.$STANDARD.gradientColors,
          radius: ["58%", "78%"],
          center: ["50%", "50%"],
          centerRadius: ["45%", "46%"],
          labelRich: {
            b: this.fontSize(0.26),
            d: this.fontSize(0.28),
            bHeight: this.fontSize(0.4)
          },
          length: 20,
          length2: 70,
          itemLabelShow: true, //数据扇区label是否显示
          //图例
          legend: {
            show: false
          },
          rollFlag: true
        }
      }
    };
  },
  mounted() {
    this.init(this.year)
  },
  methods: {
    // 初始化组件
    init(year){
      this.getAPI(year,this.time);
      this.getDjjgZbPieAPI(year);
      this.getZsLineAPI(year,this.time)
      
    },
    // 信访形式走势
    getZsLineAPI(year, time) {
      let timetype = "",
        _year = "";
      if (time === "年度") {
        timetype = "cy";
        _year = "[now-4, now]";
      } else {
        timetype = "cm";
        _year = year;
      }
    },
    // 信访形式走势-时间切换
    zsTimeClick(time) {
      this.getZsLineAPI(this.year, time);
    },
    getAPI(year,time) {
      // 1.信访总量-总数
      get_http("mdfxyjypxzfxZbTotal", {
        value: this.mdgjhjjc,
        year:year
        })
        .then(res => {
          this.leftallcount = res.data.data[0].value;
        })
        .catch(err => {
          console.log(err);
        });
      // 2.信访总量-饼图
      get_http("mdfxyjypxzfxZbPie", { 
        name:this.xfxs,
        value:this.mdgjhjjc,
        year:year
        })
        .then(res => {
          this.zbPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      // 3.整体化解率
      get_http("mdfxyjypxzfxHjvValue", {
        value:this.mdgjhjjc,
        year:year
        })
        .then(res => {
          this.hjvValue = (res.data.data[0].value * 100).toFixed(2);
        })
        .catch(err => {
          console.log(err);
        });
      // 4.年度信访件总量、化解趋势
      get_http("mdfxyjypxzfxZlqsLine", { year: year, timetype: time })
        .then(res => {
          this.zsLineParams.option.axisCategoryName =
            time === "年度" ? "年" : "月";
          this.zsLineParams.map = res.data.map;
          this.zsLineParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      // 5.国家交办、督办矛盾化解信访件-总数
      get_http("mdfxyjypxzfxGjmdhjTotal", {
        value: this.mdgjhjjc,
        year:year
        })
        .then(res => {
          this.allcount = res.data.data[0].value;
        })
        .catch(err => {
          console.log(err);
        });
      // 6.国家交办、督办矛盾化解信访件-列表
      get_http("mdfxyjypxzfxGjmdhjList", {
        name:this.jsfs,
        value:this.mdgjhjjc,
        main_name:this.main_name,
        year:year
        })
        .then(res => {
          this.detailList = res.data;
        })
        .catch(err => {
          console.log(err);
        });
      // 7.国家交办、督办矛盾化解信访件-交办
      get_http("mdfxyjypxzfxGjmdhjjbPie", { 
        name:this.xfxs,
        value:this.mdgjhjjc,
        jsfs:this.jbfs,
        year:year 
        })
        .then(res => {
          this.jbPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      // 8.国家交办、督办矛盾化解信访件-督办
      get_http("mdfxyjypxzfxGjmdhjdbPie", { 
        name:this.xfxs,
        value:this.mdgjhjjc,
        jsfs:this.dbfs,
        year:year })
        .then(res => {
          this.dbPieParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      // this.getZsLineAPI(year, "年度");    
    },
    // 9.省、市、县登记矛盾化解信访件
    getDjjgZbPieAPI(year) {
      for (let item of this.djjgArr.values()) {
        item.params.options = this.djjgPieParams.options;
        get_http("mdfxyjypxzfxSsxmdhj", { 
          year:year, 
          djjg: item.name,
          name:this.xfxs,
          value:this.mdgjhjjc
          })
          .then(res => {
            console.log('圆环',res.data.data)
            item.params.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    fontSize(res) {
      let docEl = document.documentElement,
        clientWidth =
          window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth;
      if (!clientWidth) return;
      let fontSize = 100 * (clientWidth / 1920);
      return res * fontSize;
    }
  },
  computed: {
    date() {
      return this.$store.state.date;
    },
    year() {
      return this.$store.state.year;
    }
  },
  watch: {
    date(val) {
      this.getAPI(val);
    },
    year(val) {
      this.getAPI(val);
      this.getDjjgZbPieAPI(val);
    }
  }
};
</script>

<style lang="stylus" scoped>
