  color #fefefe
  font-size 0.18rem
  text-align center
  font-weight 600
.inner3
   display flex
   justify-content space-around
   align-items center
  .chart
    margin-top 30px
    width 400px
    height 200px
.inner2
  display flex
  justify-content space-around
  align-items center
.label
  font-size 0.2rem
  text-align center
  color #fefefe
  font-weight 600
.wrapper
  position relative
  top 0.8rem
  height calc(100% - 0.8rem)

  .content
    width 100%
    height 100%
    display flex
    padding .2rem .3rem
    box-sizing border-box

    .c-left
      width 12rem
      flex 1
      height 100%
      position relative
      display flex
      flex-direction column
      border-right 1px dashed #91e3f7
      .c-top
        flex 1
        display flex
        .r-left
          flex 1
          display flex
          flex-direction column
          width 100%
          height 100%
          .inner
            height 100%
            display flex
            width 100%
            position relative
            .detail
              position absolute
              top .2rem
              left .6rem
              flex 2
              box-sizing border-box
              display flex
              justify-content space-evenly
              align-items center
              .all-count
                flex 1
              .inner
                flex 2.5
                display flex
                height 100%
                .detail-list
                  flex 1
            .chart
              padding-top 20%
              width 60%
              height 80%
              flex 1
        .r-right
          flex 1
          display flex
          flex-direction column
          width 100%
          height 100%
          .inner
            padding-top 20%
      .c-btm
        flex 1
        display flex
        .part
          flex 1
          display flex
          flex-direction column
          width 100%
          height 100%
    .c-right
      flex 1
      height 100%
      position relative
      display flex
      flex-direction column

      .r-top
        flex 1
        position relative
        display flex
        margin-top 30px
        justify-content center
        .r-top-l
          flex 1
          flex-direction column
          display flex
          justify-content center
          .all-count
            margin .1rem 0
        .inner2 
          .chart
            width invert
            height invert
            margin-top 0
          
        .zs-item
          flex 1
          height 100%
          display flex
          flex-direction column
          justify-content center
          align-items center
          p
            height 0.7rem
            display flex
            justify-content center
            align-items center
            .inner
              margin 0 .2rem
            > label
              width 1rem
              text-align right
              color #fff
              font-size 0.16rem

            span
              flex 1
              color #ffde00
              font-size 0

              label
                width auto
                color #00b0ff
                font-size 0.16rem
                margin-right .1rem

              .countSymbol
                font-size 0.16rem

              .number
                font-size 0.16rem

              .unit
                font-size 0.16rem

      .r-bottom
        flex 1
</style>
